.scroll-content {
  display: inline-block;
  width: 100%;
}

.scroll-content + .scroll-bar {
  display: inline-block;
  width: 12px;
  overflow: hidden;
  opacity: 0;
  transition: opacity 0.5s cubic-bezier(0, 1, 1, 1);
}

.scroll-content + .scroll-bar:hover, .scroll-content + .scroll-bar.scroll-active {
  opacity: 1;
}

.scroll-content + .scroll-bar > .bar {
  width: 10px;
  margin: 0 1px;
  background: rgba(0, 0, 0, 0.3);
  opacity: .8;
  border-radius: 16px;
  overflow: hidden;
  transition: background .3s ease-in-out;
}

.scroll-content + .scroll-bar > .bar:hover {
  opacity: 1;
}

.scroll-content + .scroll-bar > .bar:active {
  opacity: 1;
  background: rgba(0, 0, 0, 0.5);
}

.modal {
  position: fixed;
  display: none;
  width: 800px;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  left: 0;
  right: 0;
  top: 0;
  margin: 1.75rem auto;
  background: #fff;
  color: initial;
  text-align: left;
  z-index: 2;
}

.modal + .modal-backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 1;
}

.tooltip {
  position: absolute;
  background: #232F34;
  font-size: 12px;
  border-radius: 2px;
  padding: 3px 8px;
}
